
#docs-navbar {
  padding: 16px;
  background: #333333;
  color: #aaa;
  font-size: 0.9rem;
}
#docs-navbar select, #docs-navbar input {
    display: inline-block;
    background: #333333;
    color: white;
    font-family: Roboto, "Source Sans Pro", Calibri, Arial, sans-serif;
    font-size: 0.85rem;
    border: 1px solid #aaa;
}
#docs-navbar select {
    margin-left: 6px;
}
#docs-navbar input {
    display: block;
    float: right;
    padding: 3px 2px;
    border: none;
    border-bottom: 1px solid #aaa;
    transition: border-color 0.3s;
}
#docs-navbar input:focus {
      border-color: #2196F3;
      outline: none;
}
@media (max-width: 46.24em) {
#docs-navbar input {
        display: none;
}
}
#docs-navbar a {
    float: right;
    color: white;
}
@media (min-width: 46.25em) {
#docs-navbar a {
        display: none;
}
}
#docs-navbar .sk-folding-cube {
    position: relative;
    top: 3px;
    left: 10px;
    width: 16px;
    height: 16px;
}



