
#file-viewer code {
  font-family: "Roboto Mono", "Source Code Pro", Consolas, monospace;
  font-size: 0.8rem;
}
#file-viewer :not(pre) > code {
  background: whitesmoke;
  border-radius: 2px;
}
#file-viewer table {
  display: block;
  max-width: 100%;
  overflow: auto;
}



