
header {
  height: 3rem;
  line-height: 3rem;
  background: #222;
}
header a {
    display: inline-block;
    padding: 0 16px;
    text-decoration: none;
    color: white;
}
header .container > a {
    background: #2196F3;
    font-size: 1.1rem;
}
header .container > a:hover {
      color: white;
      background: #0c7cd5;
}
header nav {
    float: right;
}
header nav a:hover {
      color: white;
      background: #090909;
}



