
.param-table {
  border-collapse: collapse;
  border: 1px solid #e2e2e2;
}
.param-table thead {
    margin: 0;
}
.param-table th {
    background: #2196F3;
    color: white;
    font-weight: bold;
    text-transform: uppercase;
    padding: 16px;
}
.param-table th, .param-table td {
    padding: 16px;
    text-align: center;
}
.param-table tr {
    background: #f9f9f9;
}
.param-table tr:nth-child(odd) {
    background: #f6f6f6;
}
@media (max-width: 61.24em) {
.param-table {
      width: 100%;
      max-width: 100%;
}
}
.param-table-wrapper {
  overflow: auto;
}



