
#typedef-viewer h1 {
  display: inline-block;
}
#typedef-viewer h2 {
  font-size: 1.3rem;
  margin-top: 40px;
}
#typedef-viewer #typedef-types {
  font-family: "Roboto Mono", "Source Code Pro", Consolas, monospace;
}
#typedef-viewer .param-table-wrapper {
  border: 0;
}
#typedef-viewer code {
  font-family: "Roboto Mono", "Source Code Pro", Consolas, monospace;
  font-size: 0.8rem;
}
#typedef-viewer :not(pre) > code {
  background: whitesmoke;
  border-radius: 2px;
}



