
#class-overview {
  display: flex;
  flex-wrap: wrap;
  margin: 48px 0;
  min-width: 0;
}
#class-overview .title {
    font-size: 16px;
    font-weight: bold;
    color: #515151;
}
#class-overview ul {
    margin: 0;
    padding: 0;
    font-size: 0.875rem;
    color: #6a6a6a;
}
#class-overview ul li {
      display: block;
      margin: 0;
      padding: 0;
}
#class-overview ul li a {
        display: block;
        padding: 3px 0;
        color: inherit;
        text-decoration: inherit;
}
@media (min-width: 46.25em) {
#class-overview ul li a {
            padding-left: 6px;
            border-left: 2px solid #efefef;
}
}
#class-overview ul li a:hover {
          color: #2196F3;
          background: #f9f9f9;
}
#class-overview ul li a:hover .small-badge {
            background: #51adf6;
}
#class-overview ul li a:hover .small-badge.warn {
              background: #ff8080;
}
@media (min-width: 46.25em) {
#class-overview ul li a:hover {
              border-left: 2px solid;
}
}
#class-overview .col {
    flex: 1 1 auto;
    min-width: 100px;
}
@media (max-width: 46.24em) {
#class-overview {
      flex-direction: column;
}
#class-overview li {
        padding: 6px 0;
}
#class-overview .col {
        margin: 1rem 0;
        padding: 1rem;
        border: 1px solid #efefef;
}
}



