
footer {
  padding: 3rem;
  background: #222;
  color: #888;
}
footer a {
    text-decoration: none;
    color: #888;
}
footer a:hover {
    color: #a2a2a2;
}
footer .container {
    text-align: center;
}
footer .stats {
    padding: 0;
    list-style: none;
}
footer .stats li {
      display: inline-block;
}
footer .stats li:not(:last-child):after {
        content: ',';
}



