
#docs-body {
  background: #fefefe;
}
#docs-body .sk-folding-cube {
    display: block;
    margin: 200px auto;
}
#docs-body .sk-cube:before {
    background: #fefefe;
}
#docs-error {
  padding: 50px 0;
  text-align: center;
  font-size: 1.5rem;
}
#docs-error pre {
    font-size: 1.0rem;
}
[data-scrolled] {
  transition: background 1s;
}
[data-scrolled='true'] {
  background: #f6f651;
}



